/*-------------------------------------
zTree Style
version:	3.5.19
-------------------------------------*/
// bootstrap 默认Less
@import "bootstrap/variables.less";
// 思诺博UI Less
@import "sinoui-variables.less";    // 变量

.ztree {padding:0;margin:0;}
.ztree li{padding:0; margin:0;margin-top:-1px; list-style:none; line-height:14px; text-align:left; outline:0}
.ztree li ul{ margin:0; padding:0 0 0 18px}
.ztree li > ul{ padding-top: 10px; padding-bottom: 10px;}
.ztree li > ul > li > ul{ padding-top: 0; padding-bottom: 0;}
.ztree li ul.line{ background:url(../images/line_conn.gif) 0 0 repeat-y;}

.ztree li a {margin:0; cursor:pointer; height:28px; color:@ztree-bar-color; background-color: transparent;text-decoration:none; vertical-align:middle;width:100%;position:relative; display: inline-block; white-space:nowrap;text-overflow:ellipsis;overflow: hidden;}
.ztree li a.level0{border-bottom:1px #ededed solid;border-top:1px #ededed solid;background:@sino-form-bg-color; height:34px; }
.ztree li a.level0 span{font-weight:bold;line-height:34px;color:@navbar-menu-color}
.ztree li a:hover {text-decoration:underline}
.ztree li a.curSelectedNode {background-color:#e9f6ff;}
.ztree li a.curSelectedNode_Edit {padding-top:0px; background-color:#FFE6B0;height:16px; border:1px #FFB951 solid; opacity:0.8;}
.ztree li a.tmpTargetNode_inner {padding-top:0px; background-color:#316AC5;  height:16px; border:1px #316AC5 solid;
	opacity:0.8; filter:alpha(opacity=80)}
.ztree li a.tmpTargetNode_prev {}
.ztree li a.tmpTargetNode_next {}
.ztree li a input.rename {height:14px; width:80px; padding:0; margin:0;
	font-size:12px; border:1px #7EC4CC solid; *border:0px}
.ztree li span {line-height:24px; margin-right:2px;}
.ztree li span.button {line-height:0; margin:0; width:16px; height:16px; display: inline-block; vertical-align:middle;
	border:0 none; cursor: pointer;outline:none;
	background-color:transparent; background-repeat:no-repeat; background-attachment: scroll;
	background-image:url("../images/zTreeStandard.png"); *background-image:url("../images/zTreeStandard.gif")}

.ztree li span.button.chk {width:13px; height:13px; margin:0 3px 0 0; cursor: auto}
.ztree li span.button.chk.checkbox_false_full {background-position:0 0}
.ztree li span.button.chk.checkbox_false_full_focus {background-position:0 -14px}
.ztree li span.button.chk.checkbox_false_part {background-position:0 -28px}
.ztree li span.button.chk.checkbox_false_part_focus {background-position:0 -42px}
.ztree li span.button.chk.checkbox_false_disable {background-position:0 -56px}
.ztree li span.button.chk.checkbox_true_full {background-position:-14px 0}
.ztree li span.button.chk.checkbox_true_full_focus {background-position:-14px -14px}
.ztree li span.button.chk.checkbox_true_part {background-position:-14px -28px}
.ztree li span.button.chk.checkbox_true_part_focus {background-position:-14px -42px}
.ztree li span.button.chk.checkbox_true_disable {background-position:-14px -56px}
.ztree li span.button.chk.radio_false_full {background-position:-28px 0}
.ztree li span.button.chk.radio_false_full_focus {background-position:-28px -14px}
.ztree li span.button.chk.radio_false_part {background-position:-28px -28px}
.ztree li span.button.chk.radio_false_part_focus {background-position:-28px -42px}
.ztree li span.button.chk.radio_false_disable {background-position:-28px -56px}
.ztree li span.button.chk.radio_true_full {background-position:-42px 0}
.ztree li span.button.chk.radio_true_full_focus {background-position:-42px -14px}
.ztree li span.button.chk.radio_true_part {background-position:-42px -28px}
.ztree li span.button.chk.radio_true_part_focus {background-position:-42px -42px}
.ztree li span.button.chk.radio_true_disable {background-position:-42px -56px}

.ztree li span.button.switch {width:18px; height:18px}
.ztree li span.button.root_open{background-position:-92px -54px}
.ztree li span.button.root_close{background-position:-74px -54px}
.ztree li span.button.roots_open{background-position:-92px 0}
.ztree li span.button.roots_close{background-position:-74px 0}
.ztree li span.button.center_open{background-position:-92px -18px}
.ztree li span.button.center_close{background-position:-74px -18px}
.ztree li span.button.bottom_open{background-position:-92px -36px}
.ztree li span.button.bottom_close{background-position:-74px -36px}
.ztree li span.button.noline_open{background-position:-92px -72px}
.ztree li span.button.noline_close{background-position:-74px -72px}
.ztree li span.button.root_docu{ background:none;}
.ztree li span.button.roots_docu{background-position:-56px 0}
.ztree li span.button.center_docu{background-position:-56px -18px}
.ztree li span.button.bottom_docu{background-position:-56px -36px}
.ztree li span.button.noline_docu{ background:none;width:0;height:0;}
.ztree li span.button.dropdown,
.ztree li span.button.dropup
{position:absolute;top:5px;right:0;bottom:0;margin:auto;background-position:-2px -77px;}
.ztree li span.button.dropup{background-position:-42px -77px;}

.ztree li span.button.ico_open{margin-right:5px;margin-left:4px; background-position:-110px -16px; vertical-align:middle; *vertical-align:middle}
.ztree li span.button.ico_close{margin-right:5px;margin-left:4px; background-position:-110px 0; vertical-align:middle; *vertical-align:middle}
.ztree li span.button.ico_docu{width:16px;height:16px;margin-right:2px; background-position:-16px -77px; vertical-align:middle; *vertical-align:middle;}
.ztree li span.button.edit {margin-right:2px; background-position:-110px -48px; vertical-align:middle; *vertical-align:middle}
.ztree li span.button.remove {margin-right:2px; background-position:-110px -64px; vertical-align:middle; *vertical-align:middle}

.ztree li span.button.ico_loading{margin-right:2px; background:url(../images/loading.gif) no-repeat scroll 0 0 transparent; vertical-align:top; *vertical-align:middle}

ul.tmpTargetzTree {background-color:#FFE6B0; opacity:0.8; filter:alpha(opacity=80)}

span.tmpzTreeMove_arrow {width:16px; height:16px; display: inline-block; padding:0; margin:2px 0 0 1px; border:0 none; position:absolute;
	background-color:transparent; background-repeat:no-repeat; background-attachment: scroll;
	background-position:-110px -80px; background-image:url("../images/zTreeStandard.png"); *background-image:url("../images/zTreeStandard.gif")}

ul.ztree.zTreeDragUL {margin:0; padding:0; position:absolute; width:auto; height:auto;overflow:hidden; background-color:#cfcfcf; border:1px #00B83F dotted; opacity:0.8; filter:alpha(opacity=80)}
.zTreeMask {z-index:10000; background-color:#cfcfcf; opacity:0.0; filter:alpha(opacity=0); position:absolute}
.ztree li span.button.icon01_ico_docu{margin-right:2px; background: url(../images/3.png) no-repeat scroll 0 0 transparent; vertical-align:top; *vertical-align:middle;width:16px;height:16px;}
